<template>
  <div class="container">
    <div class="show default">
      <p class="info">default</p>
      <iclock :display="clock1" class="clock clock1" />
    </div>
    <div class="show emoji">
      <p class="info">You can choose emoji: smile  |  angry  |  jiong</p>
      <iclock :display="clock2" class="clock clock2" />
      <iclock :display="clock3" class="clock clock3" />
      <iclock :display="clock4" class="clock clock4" />
    </div>
    <div class="show type">
      <p class="info">You can choose type: scroll  |  clock  |  text</p>
      <iclock :display="clock5" class="clock clock5" />
      <iclock :display="clock6" class="clock clock6" />
      <iclock :display="clock7" class="clock clock7" />
    </div>
    <div class="show other">
      <p class="info">You can choose other: hoverAnimation  &  glasses </p>
      <iclock :display="clock8" class="clock clock8" />
      <iclock :display="clock9" class="clock clock9" />
      <iclock :display="clock10" class="clock clock10" />
    </div>
  </div>
</template>

<script>
import Iclock from './iclock/vue-iclock.vue'
export default {
  name: 'HelloWorld',
  data () {
    return {
      clock1: {
        className: '.clock1'
      },
      clock2: {
        className: '.clock2',
        emoji: 'smile'
      },
      clock3: {
        className: '.clock3',
        emoji: 'angry'
      },
      clock4: {
        className: '.clock4',
        emoji: 'jiong'
      },
      clock5: {
        className: '.clock5',
        emoji: 'smile',
        type: 'scroll'
      },
      clock6: {
        className: '.clock6',
        emoji: 'angry',
        type: 'clock'
      },
      clock7: {
        className: '.clock7',
        emoji: 'jiong',
        type: 'text',
        info: 'itagn'
      },
      clock8: {
        className: '.clock8',
        emoji: 'smile',
        type: 'scroll',
        hoverAnimation: true,
        glasses: true
      },
      clock9: {
        className: '.clock9',
        emoji: 'angry',
        type: 'clock',
        hoverAnimation: true,
        glasses: true
      },
      clock10: {
        className: '.clock10',
        emoji: 'jiong',
        type: 'text',
        info: 'itagn',
        hoverAnimation: true,
        glasses: true
      }
    }
  },
  components: {
    Iclock
  }
}
</script>
<style scoped>
.container{
  position: relative;
}
.show{
  width:100%;
  margin-top: 40px;
}
.info{
  color: #666;
  font-size: 2rem;
  font-weight: 700;
  margin-left: 70px;
}
.clock{
  display: inline-block;
  margin-left: 70px;
}
</style>



